.section-about {
  --size: 1;
  --font-size: 1;
  padding: size(60) 0;

  &__header {
    margin-bottom: size(30);
  }

  &__title {
    color: #cf8f2c;
    font-size: font-size(28);
    line-height: 1.25;
    font-family: $font-family-heading;
    font-weight: 700;
    margin: 0 0 size(30);
  }

  &__icon {
    font-size: size(98);
    flex-shrink: 0;
    margin-right: size(30);
  }

  &__desc {
    color: #cf8f2c;
    font-size: font-size(24);
    line-height: 1.25;
    font-family: $font-family-heading;
    font-weight: 500;
    flex-grow: 1;
  }

  &__content {
    color: $color-base;
    font-size: font-size(17);
    line-height: 1.5;

    p {
      &:not(:last-child) {
        margin-bottom: em(17*1.5, 17);
      }
    }
  }

  &__footer {
    color: #4b4b4b;
    font-size: font-size(17);
    line-height: 1.5;
    font-family: $font-family-heading;
    font-weight: 500;
    margin-top: size(30);

    p {
      &:not(:last-child) {
        margin-bottom: em(17*1.5, 17);
      }
    }
  }

  @include media-breakpoint-down(sm) {
    --size: 3/4;
    --font-size: 15/17;
  }
}